  --text-color: #4d4d4d;
  --text-color-light: #979797;
  --card-color: rgba(147, 197, 253, 1);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--text-color);
  white-space: nowrap;
}

button {
  color: var(--text-color-light);
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: var(text-color);
  height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  display: flex;
  flex-direction: column;
  flex: auto;
  justify-content: center;
  align-items: center;
}

#search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#search-container > div:first-child {
  font-weight: 600;
  padding-bottom: 10px;
  color: var(--text-color);
}

#search-form {
  display: grid;
  grid-template-columns: 4fr 1fr;
  gap: 5px;
}

#search-bar {
  min-width: 300px;
}

#search-bar,
#search-button {
  font-size: 1rem;
  padding: 5px 10px;
}

#search-button {
  color: var(--text-color);
}

#result-header {
  text-align: center;
}

#result-container {
  display: none;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  padding-top: 10vh;
}

footer {
  position: fixed;
  display: flex;
  justify-content: space-around;
  left: 0;
  bottom: 0;
  width: 100%;
  color: var(--text-color);
  margin-bottom: 20px;
}

footer p {
  display: flex;
  align-items: center;
}

footer img {
  padding: 5px;
}

#last-updated {
  color: var(--text-color-light);
}

#error-container {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  margin-bottom: 300px;
  color: var(--text-color);
}

#weather-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 20px;
  gap: 20px;
}

.card {
  padding: 20px;
  min-width: 250px;
  min-height: 400px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  text-align: center;
}

.temp-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.fahren-button,
.celsius-button {
  background-color: transparent;
  padding: 0px 5px;
  font-weight: 600;
  font-size: 1.5rem;
  border: none;
}

.fahren-button {
  border-right: 1px solid;
}

.celsius-button {
  border-left: 1px solid;
}

.fahren {
  display: none;
}

.celsius {
  display: none;
}

.selected {
  color: var(--text-color);
}

button:hover {
  color: var(--text-color);
}

.preferFahren > .temp-container > .fahren {
  display: block;
}

.preferCelsius > .temp-container > .celsius {
  display: block;
}

.weekday,
.condition,
.wind,
.humidity {
  color: var(--text-color);
  font-weight: 400;
  font-size: 2rem;
}

.wind,
.humidity {
  font-size: 1.5rem;
}

.icon {
  width: 64px;
  height: 64px;
}
